
.todoList{
  width: 290px;
  min-height: 140px;
  margin:40px auto;
  padding-left: 5px;
  padding-top: 5px;
  margin-top: 20px;
  margin-bottom: 50px;
}
.todoItem{
  width: 95%;
  height: 20px;
  border-bottom: 1px solid #b8b8bf;
  padding-bottom: 5px;
}
.radio{
  display: inline-block;
  width: 15px;
  height: 15px;
  float: left;
  border: 1px solid #979797;
  margin-top: 2px;
  margin-right: 5px;
  border-radius: 8px;
}
.todoItem:hover{
  color:black;
}
.radio:hover{
  border:1px solid #141414;
}
.isDone{
  text-decoration: line-through;
}
.select-type{
  background-color: #42b983;
  color:white;
}
.select{
  display: inline-block;
  width: 60px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  margin-top: 10px;
}
.select-section{
  width: 300px;
  height: 40px;
  margin: 0 auto;
  margin-top : -40px;
}




